  box-shadow: inset 0 -23px 25px rgba(0, 0, 0, .17), inset 0 -36px 30px rgba(0, 0, 0, .15), inset 0 -79px 40px rgba(0, 0, 0, .1), 0 2px 1px rgba(0, 0, 0, .06), 0 4px 2px rgba(0, 0, 0, .09), 0 8px 4px rgba(0, 0, 0, .09), 0 16px 8px rgba(0, 0, 0, .09), 0 32px 16px rgba(0, 0, 0, .09);
